<template>
  <div class="detail">
    <van-nav-bar fixed :border="false">
      <template #left>
        <van-icon class="arrow-left" name="arrow-left" size="18" @click="back"/>
      </template>
    </van-nav-bar>
    <div class="product-img">
      <img class="auto-img" :src="productData.large_img">
    </div>
    <div class="product-info">
      <div class="product-base-info common-box">
        <div class="product-name">{{productData.name}}</div>
        <div class="product-enname">{{productData.enname}}</div>
        <!-- <div class="product-like fr"> -->
          <van-icon class="product-like fr" :name="isLike?'like':'like-o'" @click="toggleLike"/>
        <!-- </div> -->
      </div>
      <!-- 规格 -->
      <div class="rules-box common-box">
        <div class="rules-item" v-for="(item,index) in productData.rules" :key="index">
          <div class="rules-item-title">{{item.title}}</div>
          <div class="rules-item-option-box clearfix" >
            <div class="rules-item-option fl" v-for="(option,i) in item.options" :key="i" :class="{active:option.isActive}" @click="toggleOption(option,index)">{{option.title}}</div>
          </div>
        </div>

      </div>
      <!-- 商品描述 -->
      <div class="product-desc common-box">
        <div class="title">商品描述</div>
        <div class="content">
          <p v-for="(item,index) in productData.desc" :key="index"> {{item}}</p>
        </div>
      </div>
    </div>
    <div class="bottom-box clearfix">
      <div class="fl">
        <div class="price">￥{{productData.price}}</div>
        <!-- <div class="info">加糖+热</div> -->
      </div>
      <div class="fr count">
        <van-stepper v-model="count" theme="round" button-size="26" disable-input />
      </div>
    </div>
    <!-- 商品导航 -->
    <van-goods-action>
      <van-goods-action-icon icon="bag-o" text="购物袋" :badge="bagCount == 0 ? '': bagCount" @click="goShopbag" />
       <van-goods-action-button color="#4a6fe7" text="加入购物袋" @click="addToShopbag"/>
      <van-goods-action-button color='#0c34ba' text="立即购买" @click="buyNow"/>
    </van-goods-action>
  </div>
</template>

<script>
import '../assets/less/detail.less'
import { utils } from '../assets/js/utils'
  export default {
    data(){
      return{
        pid: '',
        productData: [],
        count: 1,
        isLike: false,
        bagCount: 0,
      }
    },
    created(){
      this.pid = this.$route.query.pid;
      this.getProductData();
      this.findLike();
      this.findBagCount();
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      // 获取商品数据
      getProductData(){
        this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
        });

        this.axios({
          method: 'GET',
          url:'/productDetail',
          params:{
            appkey: this.appkey,
            pid: this.pid
          }
        }).then(result =>{
          this.$toast.clear();
          if(result.data.code == 600){
            // console.log('result ==> ',result)

            // 格式
            // [
            //   {
            //     title:'温度',
            //     options:[
            //       {
            //         title:'冷',
            //         isActive: false,
            //       },
            //       {
            //         title:'热',
            //         isActive: false,
            //       }
            //     ]
            //   }
            // ]

            let ruleTypes = ['tem','sugar','cream','milk'];
            let ruleData = [];

            ruleTypes.map((v,i) => {
              if(result.data.result[0][v] == ''){
                return;
              }
              let optionName = result.data.result[0][v].split('/');
              let options = [];
              optionName.map((value,index) => {
                options[index] = {
                  title: value,
                  isActive: index == 0
                }
              });

              ruleData.push({
                title: result.data.result[0][v + '_desc'],
                options
              })
            })

            // // console.log('ruleData ==> ',ruleData)
            result.data.result[0].rules = ruleData;

            // 处理商品描述数据
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            this.productData = result.data.result[0];
          }
          // // console.log('result ==> ',result)
        }).catch(err =>{
          this.$toast.clear();
          // // console.log('err ==> ',err)
        })
      },
      // 切换选项
      toggleOption(item,index){
        if(item.isActive){
          return;
        }
        // // console.log(this.productData.rules[index])
        for(let i = 0; i < this.productData.rules[index].options.length; i++){
          if(this.productData.rules[index].options[i].isActive){
            this.productData.rules[index].options[i].isActive = false;
          }
        }
        item.isActive = true;
      },
      // 切换喜欢
      toggleLike(){
        // this.isLike = !this.isLike;
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          // 跳转到登录页
          this.$router.push({name: 'Login'});
          return;
        }

        // 判断token是否合法
        this.$toast.loading({
          message: '加载中...',
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner'
        })

        let data = {
          appkey: this.appkey,
          pid: this.pid,
          tokenString: tk
        }

        data = utils.queryString(data);
        // // console.log(data)

        let url = this.isLike ? '/notlike' : '/like';

        this.axios({
          method: "POST",
          url,
          data
        }).then( result => {
          if(result.data.code == 800){
            this.$toast.clear();
          }else if(result.data.code == 900){
            // 取消收藏
            this.$toast.clear();
          }
          this.isLike = !this.isLike;
          this.$toast(result.data.msg);
          // console.log(result.data.msg);
          // console.log(url,"result =>",result);
        }).catch( err => {
          this.$toast.clear();
          // console.log(url,"err =>",err);
        })
      },
      // 查询是否收藏
      findLike(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        this.axios({
          method: "GET",
          url: "/findlike",
          params: {
            appkey: this.appkey,
            pid: this.pid,
            tokenString: tk
          }
        }).then( result => {
          if(result.data.result.length == 0){
            return;
          }
          this.isLike = true;
          // console.log(" findLike result ==> ",result)
        }).catch( err => {
          // console.log(" findLike err ==> ",err)
        })

      },
      // 加入购物袋
      addToShopbag(buyNow){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          // 跳转到登录页
          this.$router.push({name: 'Login'});
          return;
        }

        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        });

        // 获取商品规格
        let rules = [];
        this.productData.rules.map( v => {
          for(let i = 0; i < v.options.length; i++){
            if(v.options[i].isActive){
              rules.push(v.options[i].title);
              break;
            }
          }
        })
        // // console.log(rules)
        rules = rules.join('/')

        let data = {
          pid: this.pid,
          count: this.count,
          rule: rules,
          appkey: this.appkey,
          tokenString: tk
        }

        data = utils.queryString(data);

        this.axios({
          method: "POST",
          url: "/addShopcart",
          data
        }).then( result => {
          this.$toast.clear();
          if(result.data.code == 3000){
            if(result.data.status == 1){
              this.bagCount++;
            }

            // 立即购买 跳转到支付页面
            if(buyNow === true){
              this.$router.push({name: 'Pay',query: {sids: result.data.sid}})
            }
          }else{
            this.$router.push({name: "Login"})
          }
          // console.log("addShopcart result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.log("addShopcart err ==> ",err)
        })
      },
      // 查找购物袋数量
      findBagCount(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        });

        this.axios({
          method: "GET",
          url: "/findAllShopcart",
          params:{
            appkey: this.appkey,
            tokenString: tk
          }
        }).then(result => {
          if(result.data.code == 5000){
            this.$toast.clear();
            this.bagCount = result.data.result.length;
          }
          // console.log("findAllShopcart result ==> ",result);
        }).catch(err => {
          this.$toast.clear();
          // console.log("findAllShopcart err ==> ",err);
        })

      },
      // 去购物袋
      goShopbag(){
        this.$router.push({name: "ShopBag"});
      },
      // 立即购买
      buyNow(){
        this.addToShopbag(true);
      }
    },
  }
</script>
